@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
.dark {
  --bg-0: #000000;
  --bg-1: #111111;
  --bg-2: #222222;
  --bg-3: #333333;

  --text-100: #ffffff;
  --text-50: #777777;

  --accent-100: #448866;
  --accent-50: #224433;

  --encode: #cc8844;
  --decode: #4466aa;
}

*:focus {
  outline: none;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn-img {
  border: none;
  opacity: 0.5;
  background-color: transparent;
  padding: 0.5rem;
  transition: opacity 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.btn-img:hover,
.btn-img:focus {
  opacity: 1;
}

.cipherarrow {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 1fr auto 1fr;
}

.cipherarrow:focus .cipherarrow-wrapper {
  -webkit-filter: drop-shadow(2px 0px 0px var(--text-100))
    drop-shadow(0px 2px 0px var(--text-100))
    drop-shadow(-2px 0px 0px var(--text-100))
    drop-shadow(0px -2px 0px var(--text-100));
          filter: drop-shadow(2px 0px 0px var(--text-100))
    drop-shadow(0px 2px 0px var(--text-100))
    drop-shadow(-2px 0px 0px var(--text-100))
    drop-shadow(0px -2px 0px var(--text-100));
}

.cipherarrow:hover .cipherarrow-iconlist,
.cipherarrow:focus-within .cipherarrow-iconlist {
  opacity: 1;
}

.cipherarrow-iconlist {
  opacity: 0;
  display: flex;
  justify-content: center;
  transition: opacity 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherarrow-wrapper {
  padding: 2px;
  -webkit-filter: drop-shadow(2px 0px 0px var(--bg-2))
    drop-shadow(0px 2px 0px var(--bg-2)) drop-shadow(-2px 0px 0px var(--bg-2))
    drop-shadow(0px -2px 0px var(--bg-2));
          filter: drop-shadow(2px 0px 0px var(--bg-2))
    drop-shadow(0px 2px 0px var(--bg-2)) drop-shadow(-2px 0px 0px var(--bg-2))
    drop-shadow(0px -2px 0px var(--bg-2));
}

.cipherarrow-display {
  height: 4rem;
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  align-items: center;
  padding-inline: 2rem;
  background-color: var(--bg-1);
  -webkit-clip-path: polygon(
    1% 0%,
    calc(99% - 20px) 0%,
    99% 50%,
    calc(99% - 20px) 100%,
    1% 100%,
    calc(20px) 50%
  );
          clip-path: polygon(
    1% 0%,
    calc(99% - 20px) 0%,
    99% 50%,
    calc(99% - 20px) 100%,
    1% 100%,
    calc(20px) 50%
  );
}

.cipherdirection {
  font-size: 11px;
  color: var(--bg-0);
  padding: 0.1rem 0.5rem;
  border: transparent 2px solid;
  border-radius: 50px;
}

.cipherdirection.encode {
  background-color: var(--encode);
}

.cipherdirection.encode:hover,
.cipherdirection.encode:focus {
  border: 2px var(--text-100) solid;
}

.cipherdirection.decode {
  background-color: var(--decode);
}

.cipherdirection.decode:hover,
.cipherdirection.decode:focus {
  border: 2px var(--text-100) solid;
}

.select {
  background: transparent;
  border: none;
  color: var(--text-50);
  margin: 0;
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.select:focus,
.select:hover {
  color: var(--text-100);
}

.select option {
  background-color: var(--bg-1);
}

.cipherbox {
  display: flex;
  flex-direction: column;
  border: var(--bg-2) solid 2px;
  flex-grow: 1;
  width: 300px;
  height: 200px;
  border-radius: 3px;
  background-color: var(--bg-1);
  transition: border-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox:focus {
  border-color: var(--text-100);
}

.cipherbox-titlebar {
  display: flex;
  align-items: center;
}

.cipherbox-select option {
  background: transparent;
}

.cipherbox-handle {
  flex-grow: 1;
}

.cipherbox-content {
  display: block;
  padding: 1rem;
  resize: none;
  border: none;
  margin: 0;
  flex-grow: 1;
  background-color: transparent;
  color: var(--text-50);
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox-content:focus {
  color: var(--text-100);
}

.btn-addcipher {
  background-color: var(--bg-1);
  color: var(--text-50);
  border: solid 2px var(--bg-2);
  padding: 0.5rem;
  border-radius: 3px;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  height: calc(4rem + 4px);
}

.btn-addcipher:hover,
.btn-addcipher:focus {
  color: var(--text-100);
  border-color: var(--text-100);
}

.app-footer {
  padding: 1rem;
  color: var(--text-50);
}

.app-title {
  font-family: "Cinzel Decorative", sans-serif;
  font-weight: normal;
  height: 4rem;
  display: flex;
}

.app-title-letter {
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}

.cipherbox-container {
  display: flex;
  align-items: center;
}

.cipherlist-container {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.app.dark {
  background-color: var(--bg-0);
  color: var(--text-100);
  align-items: center;
}

.btn-addcalculation {
  background-color: var(--bg-1);
  color: var(--text-50);
  border: solid 2px var(--bg-2);
  padding: 0.5rem;
  border-radius: 3px;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  height: 100%;
}

.btn-addcalculation:hover,
.btn-addcalculation:focus {
  color: var(--text-100);
  border-color: var(--text-100);
}

